import React, { useState, useEffect } from "react";
import clsx from 'clsx';

import { makeStyles, Theme } from "@material-ui/core";
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import DoubleArrowIcon from '@material-ui/icons/DoubleArrow';
import Grid from '@material-ui/core/Grid';
import ViewComfyIcon from '@material-ui/icons/ViewComfy';
import CalendarViewDayIcon from '@material-ui/icons/CalendarViewDay';

import EventCard from './EventCard';
import { venueList } from '../TempConstants';

interface CalendarProps {

}

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        borderRadius: "4px",
        backgroundColor: "#fff",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        margin: "20px 10px 40px 10px",
        height: 'max-content',
        padding: '20px 0',
        position: 'relative',
    },

    titleWrapper: {
        display: 'flex',
        maxWidth: '1024px',
        marginLeft: 'auto',
        marginRight: 'auto'
    },

    title: {
        fontSize: '20px',
        fontWeight: 'bold',
        textAlign: 'center',
        marginBottom: '6px',
        width: '100%'
    },

    calContainer: {
        display: 'flex',
        flexDirection: 'row',
        flexWrap: 'wrap',
        maxWidth: '1024px',
        margin: '6px auto 10px auto'
    },

    headCell: {
        minHeight: '32px',
        width: '14.2857%',
        background: '#d3d3d3',
        borderTop: '1px solid #f1f1f1',
        borderRight: '1px solid #f1f1f1',
        borderRadius: '4px',
    },

    headCellMobile: {
        minHeight: '32px',
        width: '100%',
        background: '#d3d3d3',
        borderTopLeftRadius: '4px',
        borderTopRightRadius: '20px',
    },

    cell: {
        minHeight: '75px',
        width: '14.2857%',
        background: '#bcaa99',
        borderTop: '1px solid #f1f1f1',
        borderRight: '1px solid #f1f1f1',
        borderRadius: '4px'
    },

    gigCell: {
        background: '#443850',
        cursor: 'pointer',
        overflow: 'hidden',
    },

    gigCellMobile: {
        minHeight: '110px',
        width: '25%',
        borderTop: '1px solid #f1f1f1',
        borderRight: '1px solid #f1f1f1',
        borderRadius: '4px',
        background: '#443850',
        cursor: 'pointer',
        overflow: 'hidden',
    },

    disabled: {
        background: '#e2e2e2'
    },

    date: {
        padding: '5px 5px 0 5px',
        textAlign: 'right',
        fontSize: '16px',
        [theme.breakpoints.only('xs')]: {
            fontSize: '14px'
        }
    },

    day: {
        padding: '5px',
        textAlign: 'center',
        fontSize: '14px',
        fontWeight: 'bold',
    },

    prev: {
        transform: 'rotate(180deg)',
        cursor: 'pointer',
        marginLeft: '5px',
        color: '#4165eb'
    },

    next: {
        cursor: 'pointer',
        marginRight: '5px',
        color: '#4165eb'
    },

    eventTitle: {
        fontSize: '16px',
        color: '#fff',
        fontWeight: 'bold',
        textAlign: 'center',
        [theme.breakpoints.only('xs')]: {
            fontSize: '14px'
        }
    },

    eventMore: {
        fontSize: '12px',
        color: '#fff',
        fontWeight: 'bold',
        textAlign: 'right',
        paddingRight: '5px',
        paddingBottom: '5px'
    },

    selectedDate: {
        fontWeight: 'bold',
        color: '#fff'
    },

    calIcon: {
        cursor: 'pointer',
        width: '30px',
        color: '#4165eb',
        float: 'right',
    },

    today: {
        color: '#4165eb',
        cursor: 'pointer',
        marginLeft: '30px',
        textDecoration: 'underline'
    }

}));


let gigs = new Array();

function initTestGigs(currentMonth: number, currentYear: number) {
    gigs = new Array();

    for (let i = 0; i < 5; i++) {
        let venue = (venueList[getRandomArbitrary(0, 10)]);
        let event = {
            date: new Date(currentYear, currentMonth, getRandomArbitrary(1, 32)),
            eventTitle: 'performing 7pm onwards',
            venueLogoUrl: venue.logoUrl,
            venueName: venue.name,
            venueLocation: venue.location,
            bandLogoUrl: "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQHiFyUz1FpbGtH5Br07xDiBMMVrrZMLoK6t-Qqht8kMNjIbUOj&usqp=CAU",
            bandName: 'Lannister'
        };
        gigs.push(event);
    }
}

initTestGigs(new Date().getMonth(), new Date().getFullYear());

function getRandomArbitrary(min: number, max: number) {
    return Math.floor(Math.random() * (max - min) + min);
}


function getGigForDate(date: number) {
    let gigListforDate = new Array();
    gigs.map((item) => {
        if (item.date.getDate() == date) {
            gigListforDate.push(item);
        }
    });
    return gigListforDate;
}


const days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
const daysMobile = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];


const Calendar: React.FC<CalendarProps> = () => {

    const classes = useStyles();
    const mobile = useMediaQuery(useTheme().breakpoints.only('xs'));

    const today = new Date();
    const [currentMonth, setCurrentMonth] = useState((today.getMonth()));
    const [currentYear, setCurrentYear] = useState(today.getFullYear());

    const [selectedGigs, setSelectedGigs] = useState(new Array());
    const [showFullCalendar, setShowFullCalendar] = useState(false);

    const handleClick = (gigs: any) => {
        setSelectedGigs(gigs);
    }


    var firstDay = new Date(currentYear, currentMonth, 1).getDay();
    var lastDate = new Date(currentYear, currentMonth + 1, 0).getDate();


    var date = 1;
    let calArray = new Array();

    [...Array(lastDate + firstDay)].map((x, i) => {

        if (i < firstDay) {
            if (!(mobile && !showFullCalendar)) {
                calArray.push(<div className={clsx(classes.cell, classes.disabled)}></div>);
            }
        } else {
            let localGigs = getGigForDate(date);
            let gigs = new Array();
            if (localGigs.length > 0) {
                gigs.push(<div className={classes.eventTitle}>{localGigs[0].venueName}</div>)
            }

            if (localGigs.length > 1) {
                gigs.push(<div className={classes.eventMore}>+ {localGigs.length - 1} more</div>)
            }
            if (mobile && !showFullCalendar) {
                if (gigs.length > 0) {
                    calArray.push(<div onClick={() => { handleClick(localGigs) }} className={classes.gigCellMobile}>
                        <div className={classes.headCellMobile}><div className={classes.day}>{days[i % 7]}</div> </div>
                        <div className={clsx(classes.selectedDate, classes.date)}>{date}</div>
                        {gigs}
                    </div>)
                }
                date++;
            } else {
                calArray.push(<div onClick={() => { handleClick(localGigs) }} className={gigs.length > 0 ? clsx(classes.cell, classes.gigCell) : classes.cell}>
                    <div className={gigs.length > 0 ? clsx(classes.selectedDate, classes.date) : classes.date}>{date++}</div>
                    {gigs}
                </div>)
            }
        }
    })

    if (!(mobile && !showFullCalendar)) {
        while ((firstDay + date - 1) % 7 != 0) {
            calArray.push(<div className={clsx(classes.cell, classes.disabled)}></div>);
            date++;
        }
    }


    const handleNext = () => {
        if (currentMonth + 1 > 11) {
            setCurrentMonth(0);
            setCurrentYear(currentYear + 1);
        } else {
            setCurrentMonth(currentMonth + 1);
        }

        initTestGigs(currentMonth, currentYear);
        setSelectedGigs(new Array());
    }

    const handlePrev = () => {
        if (currentMonth - 1 < 0) {
            setCurrentMonth(11);
            setCurrentYear(currentYear - 1);
        } else {
            setCurrentMonth(currentMonth - 1);
        }
        initTestGigs(currentMonth, currentYear);
        setSelectedGigs(new Array());
    }

    const handleToday = () => {
        let today = new Date();
        setCurrentMonth(today.getMonth());
        setCurrentYear(today.getFullYear());
        setSelectedGigs(new Array());

    }
    let headers = new Array();

    if (mobile) {
        if (showFullCalendar) {
            daysMobile.map(item => headers.push(<div className={classes.headCell}><div className={classes.day}>{item}</div> </div>));
        }
    } else {
        days.map(item => headers.push(<div className={classes.headCell}><div className={classes.day}>{item}</div> </div>));
    }

    return (
        <div className={classes.root}>
            <span className={classes.today} onClick={handleToday}>Today</span>

            {mobile ?
                <div className={classes.calIcon}>
                    {(showFullCalendar ?
                        <CalendarViewDayIcon onClick={() => setShowFullCalendar(false)} /> :
                        <ViewComfyIcon onClick={() => setShowFullCalendar(true)} />
                    )}</div> : null
            }

            <Grid container>
                <Grid item md={selectedGigs.length > 0 ? 10 : 12} xs={12}>
                    <div className={classes.titleWrapper}>
                        <DoubleArrowIcon className={classes.prev} onClick={handlePrev} />
                        <div className={classes.title}> <span style={{ marginRight: '5px' }}>{months[currentMonth]}</span><span style={{ marginLeft: '5px' }}>{currentYear}</span></div>
                        <DoubleArrowIcon className={classes.next} onClick={handleNext} />
                    </div>
                </Grid>

                {selectedGigs.length > 0 ?
                    <Grid item md={2} xs={12}></Grid> :
                    null}

                <Grid item md={selectedGigs.length > 0 ? 10 : 12} xs={12}>
                    <div className={classes.calContainer}>
                        {headers}
                        {calArray}
                    </div>
                </Grid>
                {selectedGigs.length > 0 ?
                    <Grid item md={2} xs={12}>
                        <Grid container>
                            {
                                selectedGigs.map((item) => {
                                    return <Grid xs={6} sm={12}><EventCard event={item} /></Grid>
                                })
                            }
                        </Grid>
                    </Grid> : null}
            </Grid>
        </div >
    );
}

export default Calendar;