import React from "react";

import { makeStyles, Theme } from "@material-ui/core";
import Icon from "@mdi/react";
import { mdiPin } from '@mdi/js';

import { formatDate } from '../util/DateHelper';
import { DateFormatEnum } from "../util/DateFormatEnum";


interface EventCardProps {
    event: any
}

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        minHeight: '90px',
        margin: '10px 0',
    },

    imgWrapper: {
        width: '50px',
        height: '50px',
        overflow: 'hidden',
        boxShadow: '0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.2)',
        borderRadius: '50%',
    },

    img: {
        width: '100%',
        height: 'auto',
    },

    floatLeft: {
        float: 'left'
    },

    floatRight: {
        float: 'right'
    },

    textAlignRight: {
        textAlign: 'right'
    },


    title: {
        marginTop: '6px',
        fontSize: '18px',
        color: '#3C4858',
        fontWeight: 'bold'
    },

    subTitle: {
        // marginTop: '6px',
        fontSize: '14px',
        color: '#6c757d',
        fontWeight: 'bold'
    },


    widget: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        borderRadius: "4px",
        backgroundColor: "#fff",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        margin: "10px 10px 20px 10px",
        height: 'max-content',
        padding: '10px 5px',
    },

    logoFlexBox: {
        display: 'flex',
        justifyContent: 'center',
        paddingBottom: '5px'
    },

    pin: {
        position: 'absolute',
        cursor: 'pointer',
        color: '#443850'
    }
}));


const EventCard: React.FC<EventCardProps> = ({ event }) => {

    const classes = useStyles();
    return (
        <div className={classes.widget}>
            {/* <span className={classes.pin}><Icon path={mdiPin} size={1.2} /></span>*/}
            <a className={classes.logoFlexBox}>
                <div className={classes.imgWrapper} style={{ marginRight: '5px' }}>
                    <img className={classes.img} src={event.bandLogoUrl} alt="..." />
                </div>

                <div className={classes.imgWrapper} style={{ marginLeft: '5px' }}>
                    <img className={classes.img} src={event.venueLogoUrl} alt="..." />
                </div>

            </a>

            <div style={{ textAlign: 'center', padding: '0 5px' }}>
                <div className={classes.title} >{event.bandName}</div>
                <div className={classes.subTitle}>{event.eventTitle}</div>
                <div className={classes.subTitle}>{formatDate(event.date, DateFormatEnum.DDMMYYYY)}</div>
                <div className={classes.title}> {event.venueName}</div>
                <div className={classes.subTitle}> {event.venueLocation}</div>
            </div>

            <a>

            </a>
        </div>
    );
}

export default EventCard;